import React, { useEffect, useRef, useState } from 'react';
import Editor from '@monaco-editor/react';
import Frame from 'react-frame-component';
import BaseAppPage from '../../components/Builder';
import { Box } from '@mui/material';
import { transform } from '@babel/standalone';

const ReactAppPage = ({ location, placeholder }) => {
  // Use state to manage files dynamically
  const [files, setFiles] = useState({
    '/App.js': {
      code: `import React from 'react';

const App = () => {
  return (
    <div>
      <h1>Welcome to React App!</h1>
      <p>Edit this code to build your React application.</p>
    </div>
  );
};

export default App;`,
    },
    '/index.html': {
      code: `<!DOCTYPE html>
<html lang="en">
<head>
  <meta charset="UTF-8">
  <meta name="viewport" content="width=device-width, initial-scale=1.0">
  <title>React App</title>
</head>
<body>
  <div id="root"></div>
</body>
</html>`,
    },
  });

  const renderEditor = (files, handleFilesChange) => (
    <Box sx={{ flex: 1, height: { xs: '300px', md: '600px' }, border: '1px solid #ccc' }}>
      <Editor
        height="100%"
        width="100%"
        theme="vs-light"
        defaultLanguage="javascript"
        value={files['/App.js'].code}
        onChange={(value) => handleFilesChange('/App.js', value)}
        options={{
          minimap: { enabled: false },
          fontSize: 14,
          scrollBeyondLastLine: false,
        }}
      />
    </Box>
  );

  const iframeRef = useRef(null); // Reference to the iframe

  // Effect to inject the React app code into the iframe
  useEffect(() => {
    if (iframeRef.current) {
      const iframe = iframeRef.current;

      // Wait for the iframe to load
      iframe.addEventListener('load', () => {
        const iframeDocument = iframe.contentDocument;

        // Clear the existing content
        iframeDocument.body.innerHTML = files['/index.html'].code;

        console.log('Iframe content set:', iframeDocument.body.innerHTML); // Debugging

        try {
          // Compile the JSX code into JavaScript using Babel
          const compiledCode = transform(files['/App.js'].code, {
            presets: ['react'], // Use the React preset to handle JSX
          }).code;

          console.log('Compiled code:', compiledCode); // Debugging

          // Create a script element for React and ReactDOM
          const reactScript = iframeDocument.createElement('script');
          reactScript.type = 'module';
          reactScript.textContent = `
            import React from 'https://unpkg.com/react@18/umd/react.production.min.js';
            import ReactDOM from 'https://unpkg.com/react-dom@18/umd/react-dom.production.min.js';
            const App = () => { return /*#__PURE__*/React.createElement("div", null, /*#__PURE__*/React.createElement("h1", null, "Welcome to React App!"), /*#__PURE__*/React.createElement("p", null, "Edit this code to build your React application.")); };
  
            ReactDOM.render(<App />, document.getElementById('root'));
          `;

          reactScript.onerror = (error) => {
            console.error('Error loading script:', error);
          };

          console.log('Script content:', reactScript.textContent); // Debugging

          // Append the script to the iframe's body
          iframeDocument.body.appendChild(reactScript);
        } catch (error) {
          console.error('Error compiling JSX:', error);
        }
      });
    }
  }, [files]); // Watch for changes in the `files` state

  const handleFilesChange = (filePath, newCode) => {
    setFiles((prevFiles) => ({
      ...prevFiles,
      [filePath]: {
        code: newCode,
      },
    }));
  };

  const renderPreview = () => {
    return (
      <Box sx={{ flex: 1, height: { xs: '300px', md: '600px' }, border: '1px solid #ccc' }}>
        <Frame
          ref={iframeRef}
          style={{ width: '100%', height: '100%', border: 'none' }}
        >
          {/* Initial content for the iframe */}
          <div dangerouslySetInnerHTML={{ __html: files['/index.html'].code }} />
        </Frame>
      </Box>
    );
  };

  return (
    <>

      <BaseAppPage
      location={location}
      placeholder={placeholder}
      defaultFiles={files}
      renderEditor={(files) => renderEditor(files, handleFilesChange)}
      renderPreview={renderPreview}
      inputSystemPrompt="create a react component with good design in one file"
    />

{
        transform(files['/App.js'].code, {
          presets: ['react'], // Use the React preset to handle JSX
        }).code
      }

    </>

  );
};

export default ReactAppPage;