import React, { useState } from 'react';
import { Box, Typography, IconButton, MenuItem, Select, FormControl } from '@mui/material';
import VisibilityOffIcon from '@mui/icons-material/VisibilityOff';

export const AIChat = ({ showAI, setShowAI }) => {
  const [chatService, setChatService] = useState('deepseek'); // Default to 'deepseek'

  const handleChatServiceChange = (event) => {
    setChatService(event.target.value);
  };

  const chatServices = [
    { value: 'deepseek', label: 'DeepSeek', url: 'https://chat.deepseek.com/' },
    { value: 'you', label: 'You.com', url: 'https://you.com/' },
  ];

  const selectedService = chatServices.find(service => service.value === chatService);

  return (
    <Box sx={{ flex: 1, border: '1px solid #ccc', borderRadius: '4px', overflow: 'hidden', height: '600px' }}>
      <Box sx={{ display: 'flex', justifyContent: 'center', alignItems: 'center', borderBottom: '1px solid #ccc', height: '48px', position: 'relative' }}>
        <Typography variant="h6" component="h2" sx={{ display: 'flex', alignItems: 'center', justifyContent: 'center', flex: 1, margin: 0 }}>
          AI Chat
        </Typography>
        <Box sx={{ position: 'absolute', right: 0, display: 'flex', alignItems: 'center', gap: 1 }}>
          <FormControl variant="outlined" sx={{ minWidth: 100, height: '36px' }}> {/* Smaller width and height */}
            <Select
              value={chatService}
              onChange={handleChatServiceChange}
              sx={{ 
                height: '100%', 
                display: 'flex', 
                alignItems: 'center', 
                fontSize: '0.875rem', // Smaller font size
                padding: '6px 32px 6px 12px', // Smaller padding
              }}
            >
              {chatServices.map((service) => (
                <MenuItem 
                  key={service.value} 
                  value={service.value} 
                  sx={{ fontSize: '0.875rem' }} // Smaller font size for menu items
                >
                  {service.label}
                </MenuItem>
              ))}
            </Select>
          </FormControl>
          <IconButton onClick={() => setShowAI(false)} sx={{ color: 'primary.main' }}>
            <VisibilityOffIcon />
          </IconButton>
        </Box>
      </Box>
      <Box sx={{ height: 'calc(100% - 48px)' }}>
        <iframe
          src={selectedService.url}
          title={selectedService.label}
          width="100%"
          height="100%"
          style={{ border: 'none' }}
        />
      </Box>
    </Box>
  );
};